import * as React from "react";
import {createRoot} from 'react-dom/client';

import {baseSetting} from "../settings";
import {typeset} from "../math";
import * as helper from "./helper";
import {DFA} from "./dfa";
import {NFA} from "./nfa";
import {ENFA} from "./enfa";

declare global {
  interface Window {
    automata: Automata;
    nodeMap: {[key: number]: HTMLElement};
  }
}

export interface Automata {
  input: Input<any>;
  getNode: (state: number) => string;
  getName: (state: number) => string;
  symbolSet: Set<string>;
  finalStateSet: Set<number>;

  title: string;
  definition: string;
  diagram: string;
  table: JSX.Element;

  init: number[];
  step: (qs: number[], a: string) => number[];
  getNonValidSymbols: (w: string) => Set<string>;
  accept: (w: string) => boolean;
}

export interface Input<T> {
  kind: "dfa" | "nfa" | "enfa";
  index: number;
  data: T;
  mapping?: {[key: string]: string | number[]};
}

function loadAutomata(kind: string, input: Input<any>): Automata {
  switch (kind) {
    case 'dfa': return new DFA(input);
    case 'nfa': return new NFA(input);
    case 'enfa': return new ENFA(input);
    default: throw new Error(`INVALID KIND: ${kind}`);
  }
}

const AutomataComponent = ({input}: {input: Input<any>}) => {
  const {kind} = input;
  const automata = loadAutomata(kind, input);
  const {title, definition, diagram, table} = automata;

  // settings after rendering
  React.useEffect(() => {

    // Meramid setting
    window.mermaid.initialize({});
    const diagram = document.getElementById('diagram');
    const inner = document.getElementById('inner');
    diagram.innerHTML = window.mermaid.render('inner', inner.innerText);

    setTimeout(() => {
      baseSetting();
      setting(input);
    });
  });

  return (
    <div data-com="markdown-it">
      <h1 className="math">{title}</h1>
      <h2>Definition</h2>
      <div className="math">{definition}</div>
      <h2>Transition Diagram</h2>
      <div id="diagram" className="mermaid">
        <div id="inner">{diagram}</div>
      </div>
      <div id="form">
        <div id="hidden-wrapper"><textarea id="hidden-input" /></div>
        <textarea id="word-input" placeholder="Input Word" />
        <div id="word"></div>
        <div className="line">
          <p>RESULT:</p>
          <p id="result"></p>
        </div>
        <div id="buttons">
          <button id="accept-button" className="buton buton-green">
            <span><i className="fa fa-check"></i>ACCEPT ?</span>
          </button>
          <button id="start-button" className="buton buton-blue">
            <span><i className="fa fa-play"></i>START</span>
          </button>
          <button id="next-button" className="buton buton-blue" disabled>
            <span><i className="fa fa-chevron-right"></i>NEXT</span>
          </button>
          <button id="stop-button" className="buton buton-red" disabled>
            <span><i className="fa fa-stop"></i>STOP</span>
          </button>
        </div>
      </div>
      <h2>Transition Table</h2>{table}
    </div>
  );
}

const setting = (input: Input<any>) => {
  const {kind, mapping} = input;
  const automata = loadAutomata(kind, input);

  // button settings
  let qs: number[] = [];
  let idx: number = 0;
  const formElem = document.getElementById("form") as HTMLFormElement;
  const hiddenWrapper = document.getElementById('hidden-wrapper') as HTMLDivElement;
  const hiddenInput = document.getElementById('hidden-input') as HTMLTextAreaElement;
  const wordInput = document.getElementById('word-input') as HTMLTextAreaElement;
  const wordElem = document.getElementById('word') as HTMLDivElement;
  const startButton = document.getElementById('start-button') as HTMLButtonElement;
  const nextButton = document.getElementById('next-button') as HTMLButtonElement;
  const stopButton = document.getElementById('stop-button') as HTMLButtonElement;
  const acceptButton = document.getElementById('accept-button') as HTMLButtonElement;
  const resultElem = document.getElementById('result') as HTMLElement;
  const ITER_PER_CHUNK = 100;
  function select(q: number) {
    nodeMap[q].classList.add('selected');
  }
  function activate(...buttons: HTMLElement[]) {
    buttons.map(button => button.removeAttribute('disabled'));
  }
  function deactivate(...buttons: HTMLElement[]) {
    buttons.map(button => button.setAttribute('disabled', ''));
  }
  function pass() {
    resultElem.className = 'pass';
    resultElem.innerText = 'PASS';
  }
  function fail() {
    resultElem.className = 'fail';
    resultElem.innerText = 'FAIL';
  }
  function checkValid() {
    const invalidSet = automata.getNonValidSymbols(wordInput.value);
    if (invalidSet.size > 0) {
      let invalids = Array.from(invalidSet);
      invalids.sort();
      resultElem.className = 'fail';
      resultElem.innerText = `INVALID - ${invalids.map(x => `'${x}'`).join(', ')}`;
      return false;
    }
    return true;
  }
  function setWidth() {
    hiddenWrapper.style.width = formElem.clientWidth + 'px';
  }
  setWidth()
  window.onresize = setWidth;
  wordInput.onkeydown = function addInput(event) {
    if (event.key === 'Enter') {
      accept();
      wordInput.focus();
      return false;
    }
  }
  wordInput.oninput = function addInput() {
    resultElem.innerText = '';
    hiddenInput.value = wordInput.value;
    wordInput.style.height = (hiddenInput.scrollHeight + 5) + 'px';
  }
  function check() {
    const word = wordInput.value;
    if (idx >= word.length) {
      if (qs.findIndex(q => automata.finalStateSet.has(q)) >= 0) {
        pass();
      } else {
        fail();
      }
      deactivate(nextButton);
    }
  }
  function setPointer() {
    const word = wordInput.value;
    if (idx < word.length) {
      wordElem.innerHTML = (
        `<p>${word.substring(0, idx)}</p>` +
        `<p class="next">${word[idx] || ''}</p>` +
        `<p class="remain">${word.substring(idx + 1)}</p>`
      );
    } else {
      wordElem.innerHTML = `<p>${word}</p>`;
    }
  }
  acceptButton.onclick = accept;
  async function accept() {
    deactivate(wordInput, startButton, acceptButton);
    try {
      resultElem.innerText = '';
      const word = wordInput.value;
      if (checkValid()) {
        let idx = 0;
        let qs = automata.init;
        for (const a of word) {
          qs = automata.step(qs, a);
          idx++;
          if (idx % ITER_PER_CHUNK === 0) {
            // Prevent page stuck
            await new Promise(resolve => setTimeout(resolve))
              .catch(e => {throw e});
          }
        }
        if (qs.findIndex(q => automata.finalStateSet.has(q)) >= 0) {
          pass();
        } else {
          fail();
        }
      }
    } finally {
      activate(wordInput, startButton, acceptButton);
    }
  };
  startButton.onclick = start;
  async function start() {
    deactivate(wordInput, startButton, acceptButton);
    if (checkValid()) {
      wordElem.style.height = (wordInput.clientHeight - 16) + 'px';
      wordElem.style.width = (wordInput.clientWidth - 24) + 'px';
      setPointer();
      wordElem.style.display = 'block';
      wordInput.style.display = 'none';
      resultElem.innerText = '';
      wordInput.placeholder = '';
      idx = 0;
      qs = automata.init;
      qs.map(select);
      activate(nextButton, stopButton);
      check();
    } else {
      activate(wordInput, startButton, acceptButton);
    }
  };
  nextButton.onclick = next;
  async function next() {
    const word = wordInput.value;
    const a = word[idx++];
    for (const q of qs) nodeMap[q].classList.remove('selected');
    qs = automata.step(qs, a);
    qs.map(select);
    setPointer();
    check();
  };
  stopButton.onclick = stop;
  async function stop() {
    deactivate(nextButton, stopButton);
    for (const q of qs) nodeMap[q].classList.remove('selected');
    idx = 0;
    qs = [];
    resultElem.innerText = '';
    wordElem.style.display = 'none';
    wordInput.style.display = 'block';
    wordInput.placeholder = 'Input Word';
    activate(wordInput, startButton, acceptButton);
  };

  // Get mermaid node ids.
  const nodeMap: {[state: number]: HTMLElement} = {};
  window.nodeMap = nodeMap;
  for (const node of document.getElementsByClassName('node')) {
    let match = /flowchart-(\d+)-\d+/.exec(node.id);
    if (match) nodeMap[match[1]] = node as HTMLElement;
  }

  // Modify node labels to wrap them using inline math formats.
  for (const iStr in nodeMap) {
    const i = Number(iStr);
    const node = nodeMap[i];
    const span = node.getElementsByClassName('nodeLabel')[0]
    if (Object.keys(mapping).length > 0) {
      span.classList.add('small');
    } else {
      span.classList.remove('small');
    }
    typeset(span, `\\(${helper.getNameGen(mapping)(i)}\\)`);
  }

  // Set final states.
  for (const iStr in nodeMap) {
    const i = Number(iStr);
    const node = nodeMap[i];
    const circles = node.getElementsByTagName('circle');
    if (circles.length > 0) {
      if (automata.finalStateSet.has(i)) {
        const circle = circles[0];
        const inner = circle.cloneNode() as SVGCircleElement;
        inner.setAttribute('r', String(+inner.getAttribute('r') - 5));
        inner.setAttribute('x', String(5));
        inner.setAttribute('y', String(5));
        circle.parentElement.prepend(inner);
        circle.parentElement.prepend(circle);
      }
    } else {
      const rect = node.getElementsByTagName('rect')[0];
      if (automata.finalStateSet.has(i)) {
        const inner = rect.cloneNode() as SVGRectElement;
        inner.setAttribute('height', String(+inner.getAttribute('height') - 10));
        inner.setAttribute('width', String(+inner.getAttribute('width') - 10));
        inner.setAttribute('x', String(+inner.getAttribute('x') + 5));
        inner.setAttribute('y', String(+inner.getAttribute('y') + 5));
        inner.setAttribute('rx', String(5));
        inner.setAttribute('ry', String(5));
        rect.parentElement.prepend(inner);
        rect.parentElement.prepend(rect);
      }
      rect.setAttribute('rx', String(10));
      rect.setAttribute('ry', String(10));
    }
  }

  // finally show content
  document.body.style.opacity = '1';
}

export function load(input: Input<any>) {
  const root = createRoot(document.getElementById("root"));
  return root.render(<AutomataComponent input={input} />);
}
