export function getSymbol(symbol: string) {
  if (symbol === "") {
    return `\\epsilon`;
  } else {
    return `\\textsf{${symbol}}`;
  }
}

export function getNodeGen(mapping: {[key: string]: string | number[]} = {}) {
  return (state: number): string => {
    if (state in mapping) {
      const target = mapping[state];
      if (typeof target === 'string') {
        return `(["_${target}"_])`;
      } else if (target.length === 0) {
        return `(("__"))`;
      } else {
        return `(["(${target.map(q => "q" + q).join(', ')})"])`;
      }
    } else {
      return `(("_${state}_"))`;
    }
  }
}

export function getNamesGen(mapping: {[key: string]: string | number[]} = {}) {
  const getName = getNameGen(mapping)
  return (states: number[]): string =>
    '\\{' + states.map(getName).join(', ') + '\\}'
}

export function getNameGen(mapping: {[key: string]: string | number[]} = {}) {
  return (state: number): string => {
    if (state in mapping) {
      const target = mapping[state];
      if (typeof target === 'string') {
        return `\\textsf{${target}}`;
      } else {
        return `\\{${target.map(getSimpleName).join(', ')}\\}`;
      }
    } else {
      return getSimpleName(state);
    }
  }
}

export function getSimpleName(state: number): string {
  return `q_{${state}}`;
}
