import {typeset} from "./math";

// base settings
export function baseSetting() {

  // makrdown setting
  document.querySelectorAll(`[data-com="markdown-it"]`).forEach(elem => {
    const md = window.markdownit({
      html: true,
      linkify: true,
      typographer: true
    })
    elem.outerHTML = md.render(elem.innerHTML);
  });

  // MathJax setting
  for (const elem of document.getElementsByClassName("math")) {
    typeset(elem, elem.innerHTML);
  }
}
