export function typesetClass(className: string) {
  for (const elem of document.getElementsByClassName(className)) {
    typeset(elem, elem.innerHTML);
  }
}

export function typesetId(id: string) {
  const elem = document.getElementById(id)
  typeset(elem, elem.innerHTML);
}

export function typeset(span: Element, content: string) {
  const code = () => {
    span.innerHTML = content;
    return [span];
  };
  // @ts-ignore
  MathJax.startup.promise = MathJax.startup.promise
    // @ts-ignore
    .then(() => MathJax.typesetPromise(code()))
    .catch((err: Error) => console.error("[TypesetFailed] " + err.message));
  // @ts-ignore
  return MathJax.startup.promise;
}
