import * as React from "react";
import {getNameGen, getSymbol, getNodeGen} from './helper'
import {Automata, Input} from './index'

namespace DFA {
  export interface Data {
    states: number[];
    symbols: string[];
    trans: {[key: string]: {[key: string]: number}};
    initState: number;
    finalStates: number[];
  }
}

export class DFA implements Automata {
  input: Input<DFA.Data>;
  getNode: (state: number) => string;
  getName: (state: number) => string;
  symbolSet: Set<string>;
  finalStateSet: Set<number>;

  title: string;
  definition: string;
  diagram: string;
  table: JSX.Element;
  init: number[];

  constructor(givenInput: Input<any>) {
    const input = givenInput as Input<DFA.Data>
    const {data, mapping} = input;
    const {states, symbols, initState, finalStates} = data;
    states.sort();
    symbols.sort();
    finalStates.sort();
    this.input = input;
    this.getNode = getNodeGen(mapping);
    this.getName = getNameGen(mapping);
    this.symbolSet = new Set(symbols);
    this.finalStateSet = new Set(finalStates);

    this.title = 'Deterministic Finite Automata (DFA)';
    this.definition = this.getDefinition();
    this.diagram = this.getDiagram();
    this.table = this.getTable();
    this.init = [initState];
  }

  // definition
  getDefinition(): string {
    const {states, symbols, initState, trans, finalStates} = this.input.data;
    const transElems: string[] = [];
    for (const stateStr of states) {
      const state = Number(stateStr);
      const inner = trans[stateStr];
      for (const symbol of symbols) {
        const target = inner[symbol];
        const q = this.getName(state);
        const a = getSymbol(symbol);
        const p = this.getName(target);
        transElems.push(`\\delta(${q}, ${a}) &=& ${p}\\\\`);
      }
    }
    // final result
    return `
  \\[
    A = ( Q, \\Sigma, \\delta, q_0, F )
  \\]
  \\[
    \\begin{array}{lcl}
      Q              &=& \\{ ${states.map(this.getName).join(', ')} \\}\\\\
      \\Sigma        &=& \\{ ${symbols.map(getSymbol).join(', ')} \\}\\\\
      \\delta: Q \\times \\Sigma
      \\rightarrow Q &\\text{s.t.}& \\left\\{ \\;
        \\begin{array}{lcl}
          ${transElems.join('\n')}
        \\end{array}
      \\right.\\\\
      q_0 \\in Q     &=& ${this.getName(initState)}\\\\
      F \\subseteq Q &=& \\{${finalStates.map(this.getName).join(', ')}\\}\\\\
    \\end{array}
  \\]
  `;
  }

  // transition diagram
  getDiagram(): string {
    const {states, symbols, initState, trans} = this.input.data;
    const map: {[key: string]: string[]} = {};
    states.map(q => symbols.map(a => {
      const p = trans[q][a];
      const key = `${q}:${p}`
      if (key in map) {
        map[key].push(a);
      } else {
        map[key] = [a];
      }
    }));
    const edges = states.map(q => states.map(p => {
      const key = `${q}:${p}`
      if (key in map) return `${q} -- "${map[key].join(", ")}" --> ${p};`;
      return '';
    }).join('')).join('');
    return `
    graph LR;
    ${states.map(q => `${q}${this.getNode(q)};`).join('')}
    S{{Start}} --> ${initState};
    ${edges}`;
  }

  // transition table
  getTable(): JSX.Element {
    const {states, symbols, trans} = this.input.data;
    return (
      <table>
        <thead>
          <tr key={0}>
            <th></th>
            {symbols.map((a, j) => (
              <th key={j} className="math">\({getSymbol(a)}\)</th>)
            )}
          </tr>
        </thead>
        <tbody>
          {states.map((q, i) => (
            <tr key={i + 1}>
              <td className="math">\({this.getPrefix(q) + this.getName(q)}\)</td>
              {symbols.map((a, j) => (
                <td key={(i + 1) * symbols.length + j} className="math">
                  \({this.getName(trans[q][a])}\)
                </td>
              ))}
            </tr>
          ))}
        </tbody>
      </table>
    );
  }

  // prefix for transition table
  getPrefix(q: number): string {
    const {initState} = this.input.data;
    let prefix = '';
    if (this.finalStateSet.has(q)) prefix += "* "
    if (q === initState) prefix += "\\rightarrow ";
    return prefix;
  }

  // one step
  step(qs: number[], a: string): number[] {
    const {trans} = this.input.data;
    return qs.flatMap(q => trans[q][a]);
  }

  // validity check
  getNonValidSymbols(w: string): Set<string> {
    const A = new Set(w);
    for (const a of this.symbolSet) A.delete(a);
    return A;
  }

  // accept check
  accept(w: string): boolean {
    let qs = this.init;
    for (const a of w) qs = this.step(qs, a);
    return qs.some(q => this.finalStateSet.has(q));
  }
}
